/*
  SPACING SYSTEM (px)
  2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 / 128

  FONT SIZE SYSTEM (px)
  10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98 / 112 / 126 / 142 / 158 / 176 / 194 / 214 / 234
*/

/* Use em for defining max-width in media queries. To calculate em, divide the value in px by 16 (e.g., 1200px / 16 = 75em). */

@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

:root {
  --indigo: #5c7cfa;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  color: white;
}

.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header,
footer {
  display: flex;
  background-color: var(--indigo);
  justify-content: space-between;
  align-items: center;
  padding: 1rem 10rem;
  margin-bottom: 5rem;
  gap: 3.2rem;
}

footer {
  margin-bottom: 0;
}

.favorites-icon {
  position: relative;
  display: flex;
  align-items: center;
}

.favorites-icon span {
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: #ffd43b;
  padding: 0.1em 0.5em;
  border-radius: 50%;
}

.app-logo-container {
  display: flex;
  align-items: center;
  gap: 2.4rem;
}

main {
  max-width: 130rem;
  margin: 0 auto;
  padding: 0 3rem;
  flex: 1;
}

h1 {
  font-size: 3rem;
}

li {
  list-style-type: none;
}

.loading,
.unknown-keyword {
  color: #333;
  text-align: center;
  font-weight: 600;
}

form {
  display: flex;
  justify-content: center;
  gap: 1.6rem;
}

form input {
  font-size: 1.6rem;
  border-radius: 5px;
  border: none;
  padding: 0.8em 5em 0.8em 0.8em;
}

form input:focus {
  outline: none;
}

form button {
  background-color: transparent;
  margin-left: -8.5rem;
  border: none;
  border-left: 1px solid #ced4da;
  padding: 0 2rem;
  cursor: pointer;
}

.icon {
  cursor: pointer;
}

.glass {
  height: 2.5rem;
  transform: rotate(-90deg);
}

.star {
  background: transparent;
  border: none;
}

.eras {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6.4rem;
}

.eras button {
  font-size: 1.8rem;
  color: white;
  background-color: rgba(92, 124, 250, 0.6);
  padding: 0.5em 3em;
  border: none;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.eras button:hover,
.eras button.active {
  background-color: var(--indigo);
}

.events-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 6.4rem;
  font-size: 1.8rem;
  line-height: 1.5;
  margin: 8rem 0;
}

.event {
  display: grid;
  height: fit-content;
  row-gap: 3rem;
  background-color: var(--indigo);
  color: white;
  font-weight: 500;
  border-radius: 8px;
  padding: 2rem;
  transition: all 0.2s;
}

.event-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.event:hover {
  transform: scale(1.02);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.114);
}

.event h5 {
  font-size: 1.2rem;
  font-weight: 400;
  font-style: italic;
  color: rgba(255, 255, 255, 0.687);
}

.date {
  font-size: 3rem;
}

/* DYNAMIC */

.favorite {
  fill: #ffd43b;
}

/* Responsive Design */

@media (max-width: 600px) {
  header {
    padding: 1rem 5rem;
  }

  main {
    padding: 0 2rem;
  }
}

@media (max-width: 1060px) {
  header,
  footer {
    padding: 1rem 3rem;
    flex-direction: column;
    gap: 3.2rem;
  }

  .eras {
    flex-direction: column;
    gap: 3.2rem;
  }

  form {
    gap: 1.2rem;
  }

  form input {
    width: 100%;
  }
}

@media (max-width: 37.5em) {
  header {
    padding: 1rem;
  }

  main {
    padding: 0 1rem;
  }

  .events-list {
    gap: 3.2rem;
  }

  .event {
    padding: 1rem;
  }

  .event h5 {
    font-size: 1rem;
  }

  .date {
    font-size: 2rem;
  }

  .unknown-keyword {
    font-size: 1.2rem;
    margin: 2rem 0;
  }

  .eras button {
    font-size: 1.6rem;
    padding: 0.5em 2em;
  }
}
